import React, { Component } from 'react';
import './NavBar.css';

import { slide as Menu } from 'react-burger-menu';
import {Link,withRouter} from 'react-router-dom';  // So that we can replace anchor tags with link components to allow for smooth client side routing


class NavBar extends React.Component{
    render(){

        const{location} = this.props;  //path name  

        const homeClass =location.pathname === '/' ? 'active-item' : '';
        const aboutClass =location.pathname === '/about' ? 'active-item' : '';
        const projectsClass =location.pathname === '/projects' ? 'active-item' : '';
        const skillsClass =location.pathname === '/skills' ? 'active-item' : '';
        const CertificationsClass =location.pathname === '/certifications' ? 'active-item' : '';
        const contactClass =location.pathname === '/contact' ? 'active-item' : '';
        return (
            <Menu>
                {/* We Should never use <a> inside our react router appplication 
              <a id="home" className="menu-item" href="/">Home</a> */}

              <Link to='/' className={`menu-item ${homeClass}`}> Home </Link>
              <Link to='/about' className={`menu-item ${aboutClass}`}>About Me </Link>
              <Link to='/projects' className={`menu-item ${projectsClass}`}> Projects </Link>
              <Link to='/skills' className={`menu-item ${skillsClass}`}> Skills </Link>
              <Link to='/certifications' className={`menu-item ${skillsClass}`}> Certifications </Link>
              <Link to='/contact' className={`menu-item ${contactClass}`}> Contact Me </Link>
              
            </Menu>
          );
    }
}

export default withRouter(NavBar);
